@mixin container(){
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	@include breakpoint(sm){
		width: 750px;
	}
	@include breakpoint(md){
		width: 970px;
	}
	@include breakpoint(lg){
		width: 1170px;
	}
}

@mixin btn($background, $color){
	background-color: $background;
	border-color: $background;
	border-bottom: 3px solid darken($background, 10%);
	font-weight: $sm-font-weight;
	text-transform: uppercase;
	color: $color;
	&:focus, 
	&:hover,
	&:active,
	&:visited{
		outline: none !important;
		background-color: darken($background, 5%) !important;
		border-color: darken($background, 5%) !important;
		border-bottom: 3px solid darken($background, 10%) !important;
	}
}