%clearfix{
	&:after,
	&:before {
		content: "";
		display: table;
	}
	&:after {
		clear: both;
	}
}

@mixin input-placeholder($color){
  &::-webkit-input-placeholder {
    color: $color;
  }
  &:-moz-placeholder {
    color: $color;  
  }
  &::-moz-placeholder { 
    color: $color;  
  }
  &:-ms-input-placeholder {  
    color: $color;  
  }
}

@mixin alert($alert-color) {
    border-left: 4px solid $alert-color;
    border-color: $alert-color;
    color: darken($alert-color, 5%);
    background: lighten($alert-color, 25%);
    position: relative;

    strong {
        display: block;
        text-transform: uppercase;
        font-size: $md-font-size;
    }

    .close {
        position: absolute;
        top: 10px;
        right: 15px;
    }
}


@mixin backgroundGradient($startColor, $endColor){
    background: $startColor;
    background: -moz-linear-gradient(-45deg, $startColor 0%, $endColor 100%);
    background: -webkit-linear-gradient(-45deg, $startColor 0%, $endColor 100%);
    background: linear-gradient(135deg, $startColor 0%, $endColor 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$startColor', endColorstr='$endColor',GradientType=1 );
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}