.login{
	&-body{
		height: 100%;
	}
	&-wrapper{
	    width: 100%;
	    height: 100%;
    	display: table;
	}
	&-content{
		display: none;
		@include breakpoint(md){
			width: 325px;
			height: 100%;
			padding: 50px;
			display: table-cell;
			vertical-align: middle;
			@include backgroundGradient($dark-background, lighten($dark-background, 20%));
		}
		h1{
			font-size: $lg-font-size;
			margin-bottom: 5px;
			color: #fff;
		}
		hr{
			margin-top: 5px;
			border-color: lighten($dark-background, 15%);
		    box-shadow: 0px 1px 0px 0px lighten($dark-background, 5%);
		}
		p{
			color: #aaaaaa;
		}
		p > i{
			color: #aaaaaa;
		}
	}
	&-formWrapper{
		width: 100%;
		height: 100%;
		padding: 75px;
		display: table-cell;
		vertical-align: middle;
		background: #fff;
		background-image: url('../images/login-background.png');
		background-repeat: no-repeat;
	    background-repeat: no-repeat;
	    background-size: contain;
	    background-position: bottom;
		@include breakpoint(md){
			width: auto;
		}
	}
	&-form{
		background-color: $background;
		box-shadow: 0px 0px 3px 0px #ccc;
		border-radius: 0px;
		width: 425px;
		padding: 50px 45px;
		margin: auto;
		> img{
			width: 250px;
			margin-bottom: 30px;
		}
		.form-group{
			label{
				margin-bottom: 5px;
				font-weight: $xs-font-weight;
			}
		}
		.input-group{
			width: 100%;
			.input-group-addon{
				font-size: 20px;
				background: transparent;
			    border: none;
			    position: absolute;
			    right: 15px;
			    top: 1px;
			    z-index: 9;
			    color: $light-text-color;
			}
			.form-control{
				border-radius: 0px;
				box-shadow: none;
				background-color: #fff;
				border: none;
				border-left: 2px solid $primary-color;
				&:focus{
					border: 1px solid lighten($primary-color, 20%);
					border-left: 2px solid $primary-color;

				}
			}
		}
	}
}

.page-header{
	&.stickyHeader{
		width: 100%;
		background: #fff;
		position: fixed;
		top: 250px;
		left: 0;
		z-index: 9;
		@include breakpoint(sm){
			top: 0px;
		}
		.row{
			@include container();
		}
	}
}
.user{
	&-dropdown{
		> a{
			padding-top: 6px !important;
			padding-bottom: 6px !important;
			padding-right: 35px !important;
			position: relative !important;
			@extend %clearfix;
			.caret{
				position: absolute;
			    top: 25px;
			    right: 15px;
			    color: #fff;
		    }
		}
		.dropdown-menu{
			li > a{
				i{
					font-size: 20px;
				    margin-right: 10px;
					vertical-align: middle;
					color: $light-text-color;
				}
			}
		}
	}
	&-info{
		float: right;
	}
	&-name{
		display: block;
		margin-top: 5px;
		text-transform: capitalize;
		font-size: $md-font-size;
		color: #fff;
	}
	&-role{
		display: block;
		text-transform: uppercase;
		font-size: 10px;
		color: #fff;
	}
	&-avatar{
		width: 45px;
		height: 45px;
		border-radius: 50%;
		line-height: 45px;
		margin-right: 10px;
		text-align: center;
		display: block;
		float: left;
		@include backgroundGradient($accent-color, lighten($accent-color, 10%))
		color: #fff;
		font-size: 26px;
		font-weight: 700;
	}
}
.notification{
	&-list{
		padding: 10px 20px !important;
		border-bottom: 1px solid $divider-color !important;
	}
	&-dropdown{
		> a{
			padding: 17px 15px !important;
			position: relative !important;
			@extend %clearfix;
			color: #fff !important;
			i{
				font-size: 22px;
			}
			.badge{
				position: absolute;
			    top: 10px;
			    right: 2px;
			    background-color: $accent-color;
			}
		}
		.dropdown-menu{
			padding: 0px;
			width: 400px;
		}
	}
	&-icon{
		display: block;
		width: 35px;
		height: 35px;
		border-radius: 50%;
		line-height: 35px;
		font-size: 22px;
		text-align: center;
		background-color: $primary-color;
		color: #fff;
		float: left;
		margin-top: 2px;
	    margin-right: 15px;
		i{
			margin-right: 0px;
		}
	}
	&-content{
		p{
			overflow: hidden;
			white-space: nowrap;
		    text-overflow: ellipsis;
		    margin-bottom: 0px;
		    min-width: 0px;
		}
	}
	&-date{
		font-size: $xs-font-size;
		color: $light-text-color;
	}
}
.navbar {
    &-menu {
        height: 250px;
        max-height: 250px;

        @include breakpoint(sm) {
            height: auto;
            max-height: auto;
        }

        > li {
            position: relative;

            > a {
                color: #ccc !important;
                padding-top: 15px;
                padding-bottom: 8px;

                i {
                    color: $shade-color;
                    text-shadow: 1px 0px 2px #000;

                    @include breakpoint(sm) {
                        display: block;
                        text-align: center;
                        margin: 0px;
                        margin-bottom: 8px;
                        font-size: 30px;
                    }
                }

                .caret {
                    margin-left: 5px;
                }

                &:hover,
                &:focus {
                    color: #fff !important;
                }
            }

            @include breakpoint(sm) {
                &:hover,
                &:focus,
                &.active > a {
                    background-color: darken($dark-background, 5%) !important;
                }
            }

            &:nth-child(10) > ul {
                @include breakpoint(sm) {
                    right: 0;
                }

                > li > ul {
                    @include breakpoint(sm) {
                        left: -190px;
                        right: auto;
                    }
                }
            }

            > ul {
                margin: 0px;
                padding: 0px 0px 0px 25px;
                width: 100%;
                display: block;
                z-index: 99;

                @include breakpoint(sm) {
                    background-color: darken($dark-background, 5%);
                    width: 190px;
                    padding: 0px;
                    position: absolute;
                    display: none;
                }

                > li {
                    display: block;
                    position: relative;
                    list-style: none;
                    border-bottom: 1px solid lighten($dark-background, 2%);

                    > a {
                        display: block;
                        color: #F6F6F6;
                        padding: 5px 15px;
                        position: relative;

                        &:hover,
                        &:focus,
                        &:visited,
                        &:active {
                            text-decoration: none;
                        }

                        &.dropdown:after {
                            @extend %mdi-fonts;
                            content: '\f35d';
                            position: absolute;
                            right: 15px;
                            font-size: 18px;

                            @include breakpoint(sm) {
                                content: '\f35f';
                            }
                        }

                        i {
                            margin: 0px;
                        }
                    }

                    @include breakpoint(sm) {
                        &:hover,
                        &:focus {
                            background-color: darken($dark-background, 10%);
                        }
                    }

                    > ul {
                        margin: 0px;
                        padding: 0px 0px 0px 25px;
                        width: 100%;
                        display: block;

                        @include breakpoint(sm) {
                            background-color: darken($dark-background, 10%);
                            width: 190px;
                            padding: 0px;
                            display: none;
                            position: absolute;
                            top: 0;
                            right: -190px;
                        }

                        > li {
                            display: block;
                            list-style: none;
                            border-bottom: 1px solid lighten($dark-background, 4%);

                            > a {
                                display: block;
                                color: #F6F6F6;
                                padding: 5px 15px;
                                text-decoration: none;

                                @include breakpoint(sm) {
                                    &:hover,
                                    &:focus {
                                        background-color: darken($dark-background, 15%);
                                        text-decoration: none;
                                    }
                                }

                                i {
                                    margin: 0px;
                                }
                            }
                        }
                    }

                    &:hover > ul,
                    &:focus > ul {
                        display: block;
                    }
                }
            }

            &:hover > ul,
            &:focus > ul {
                display: block;
            }
        }
    }
}

    .btn {
        &-sms,
        &-email {
            color: #fff !important;
            padding: 17px 15px !important;

            > i {
                margin: 0px;
                font-size: 22px;
            }
        }
    }

    .dashboard {
        &-container {
            padding: 15px 15px;
        }

        &-block {
            margin-top: 25px;
            margin-bottom: 25px;
        }

        &-count {
            background-color: #fff;
            border-radius: 0px;
            overflow: hidden;
            box-shadow: 0px 0px 6px 0px lighten($dark-background, 55%);
            margin-bottom: 20px;
            @extend %clearfix;

            .block {
                &-icon {
                    width: 25%;
                    height: 95px;
                    line-height: 95px;
                    float: left;
                    color: #fff;
                    text-align: center;
                    font-size: 34px;
                }

                &-content {
                    width: 75%;
                    padding: 10px 25px;
                    float: right;
                }

                &-title {
                    display: block;
                    text-transform: uppercase;
                    font-family: $title-font;
                    font-weight: $md-font-weight;
                    border-bottom: 1px solid #eee;
                    padding-bottom: 5px;
                }

                &-count {
                    display: inline-block;
                    font-family: $text-font;
                    font-weight: $xl-font-weight;
                    font-size: $xl-font-size;
                    margin-right: 10px;
                }

                &-stats {
                    margin-top: 5px;
                    display: inline-block;
                    font-size: $xs-font-size;
                    color: $light-text-color;

                    strong {
                        display: block;
                    }

                    &-increased {
                        strong {
                            color: $success-color;

                            &:after {
                                @extend %mdi-fonts;
                                content: "\f13f";
                            }
                        }
                    }

                    &-decreased {
                        strong {
                            color: $danger-color;

                            &:after {
                                @extend %mdi-fonts;
                                content: "\f13c";
                            }
                        }
                    }
                }
            }

            &.count-info {
                .block-icon {
                    background-color: $info-color;
                }
            }

            &.count-danger {
                .block-icon {
                    background-color: $danger-color;
                }
            }

            &.count-warning {
                .block-icon {
                    background-color: $warning-color;
                }
            }

            &.count-success {
                .block-icon {
                    background-color: $success-color;
                }
            }

            &.count-primary {
                .block-icon {
                    background-color: $primary-color;
                }
            }

            &.count-default {
                .block-icon {
                    background-color: $accent-color;
                }
            }
        }

        &-plan {
            padding-left: 15px;
            position: relative;

            &:before {
                content: "";
                height: 100%;
                width: 2px;
                position: absolute;
                background-color: $divider-color;
            }
        }

        &-tabs {
            display: inline-block;
            width: 100%;
            margin: 10px 0px;

            .tabs-count {
                display: block;
                font-family: $title-font;
                font-size: $lg-font-size;
                font-weight: $lg-font-weight;
                text-align: center;
                padding: 0px 0px 10px 0px;
            }
        }

        &-circle {
            text-align: center;
            margin: 25px 0px;
        }

        &-report {
            border-bottom: 1px solid $divider-color;
            padding-bottom: 15px;

            .report-link {
                padding: 0px 20px;
                position: relative;
                overflow: hidden;
                height: 55px;
                display: block;
                line-height: 55px;
                font-family: $title-font;
                font-size: $md-font-size;
                text-transform: uppercase;
                font-weight: $md-font-weight;
                color: $text-color;
                box-shadow: 0px 0px 2px 0px lighten($dark-background, 55%);
                background-color: #fff;
                margin-bottom: 10px;

                &:after {
                    content: " ";
                    position: absolute;
                    top: 11px;
                    right: 0;
                    width: 3px;
                    height: 35px;
                    background-color: lighten($primary-color, 10%);
                }

                &:before {
                    @extend %mdi-fonts;
                    content: "\F332";
                    position: absolute;
                    font-size: 32px;
                    right: 15px;
                    color: $light-text-color;
                    opacity: 0.2;
                }

                &:hover {
                    text-decoration: none;
                    color: $primary-color;

                    &:before {
                        color: $primary-color;
                    }
                }
            }
        }
    }

    @media print {
        .underwriting-container {
            h1 {
                padding: 4px 0px;
                font-size: 14px
            }

            h2 {
                padding: 2px 0px;
                font-size: 12px
            }

            em {
                font-size: 8px;
            }

            label {
                font-size: 10px;
            }

            table td,
            table th {
                padding: 2px;
                font-size: 10px;
            }
        }
    }

    .form-align-right {
        .control-label {
            text-align: left;
        }
    }

    @media print {
        .noPrint {
            display: none !important;
        }

        .print {
            page-break-after: avoid !important;
            page-break-before: avoid !important;
        }

        .print:last-child {
            page-break-after: auto;
        }
    }

.dashboard-exeuctive {
    .panel.panel-primary {
        .panel-body {
            .table {
                tr:nth-of-type(odd) {
                    background: lighten($primary-color, 45%);
                }

                tr > th {
                    background: #fff;
                    font-family: $title-font;
                    font-weight: $lg-font-weight;
                    color: darken($primary-color, 10%);
                    border-color: lighten($primary-color, 30%);
                }

                tr > td {
                }

                tr > td:first-child,
                tr > th:first-child {
                    padding-left: 20px;
                }

                tr > td:last-child,
                tr > th:last-child {
                    padding-left: 20px;
                }
            }

            &.slimScroll {
                min-height: 260px;
            }
        }
    }
}
