/* MaterialDesignIcons Fonts */
@font-face {
	font-family: "Material Design Icons";
	src: url("../fonts/materialdesignicons-webfont.eot?v=1.9.32");
	src: url("../fonts/materialdesignicons-webfont.eot?#iefix&v=1.9.32") format("embedded-opentype"), url("../fonts/materialdesignicons-webfont.woff2?v=1.9.32") format("woff2"), url("../fonts/materialdesignicons-webfont.woff?v=1.9.32") format("woff"), url("../fonts/materialdesignicons-webfont.ttf?v=1.9.32") format("truetype"), url("../fonts/materialdesignicons-webfont.svg?v=1.9.32#materialdesigniconsregular") format("svg");
	font-weight: normal;
	font-style: normal;
}
%mdi-fonts{
	display: inline-block;
	font: normal normal normal 24px/1 "Material Design Icons";
	font-size: inherit;
	text-rendering: auto;
	line-height: inherit;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

/* FontAwesome Fonts */
@font-face {
  font-family: 'FontAwesome';
  src: url('../fonts/fontawesome-webfont.eot?v=4.7.0');
  src: url('../fonts/fontawesome-webfont.eot?#iefix&v=4.7.0') format('embedded-opentype'), url('../fonts/fontawesome-webfont.woff2?v=4.7.0') format('woff2'), url('../fonts/fontawesome-webfont.woff?v=4.7.0') format('woff'), url('../fonts/fontawesome-webfont.ttf?v=4.7.0') format('truetype'), url('../fonts/fontawesome-webfont.svg?v=4.7.0#fontawesomeregular') format('svg');
  font-weight: normal;
  font-style: normal;
}
%fa-fonts{
	display: inline-block;
	font: normal normal normal 14px/1 FontAwesome;
	font-size: inherit;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.ui-datepicker{
    width: 215px !important;
    margin-top: 1px;
    border-color: $primary-color !important;
    border-radius: 0px !important;
    padding: 0px !important;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.4);
    .ui-datepicker-header{
        padding: 2px 0;
    	background: $primary-color;
    	border: none;
    	border-bottom: 1px solid $primary-color;
	 	border-radius: 0px;
    	.ui-datepicker-prev,
    	.ui-datepicker-next{
		    width: 30px;
    		height: 30px;
    		font-size: 20px;
    		border: none;
            color: #fff;
    		&:hover,
    		&:focus,
    		&:visited,
    		&:active{
    			font-size: 20px;
    			border: none !important;
    			background: transparent !important;
    		}
    		.ui-icon{
    			display: none;
    			background-image: none;
    		}
    	}
    	.ui-datepicker-prev{
    		text-indent: inherit;
    		left: 5px;
    		top: 2px;
    		&:after{
    			content: "\f141";
    			@extend %mdi-fonts;
    		}
    		&:hover,
    		&:focus,
    		&:visited,
    		&:active{
    			left: 5px;
    			top: 2px;
    		}
    	}
    	.ui-datepicker-next{
    		text-indent: inherit;
    		right: 0px;
    		top: 2px;
    		&:after{
    			content: "\f142";
    			@extend %mdi-fonts;
    		}
    		&:hover,
    		&:focus,
    		&:visited,
    		&:active{
    			right: 0px;
    			top: 2px;
    		}
    	}
        .ui-datepicker-month,
        .ui-datepicker-year{
            font-size: 12px;
            font-weight: 400;
            background-color: $primary-color;
            color: #fff;
            border: none;
            > option{
                background-color: #fff;
                color: #333;
            }
        }
    }
    .ui-datepicker-calendar{
        font-size: 12px;
        thead tr th{
            color: #333;
            font-weight: 600;
        }
        .ui-state-default{
            text-align: center;
            border: none;
            background: transparent;
            color: $primary-color;
            &:hover{
                background: #eee;
            }
            &.ui-state-active{
                background: $primary-color !important;
                color: #fff !important;
            }
            &.ui-state-highlight{
                background: lighten($primary-color, 35%);
                color: $primary-color;
            }
        }
    }
}

#ndp-nepali-box{
    width: 215px;
    border-radius: 0px !important;
    padding: 0px !important;
    border-color: $primary-color !important;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.4);
    .ndp-header{
        line-height: 28px;
        border-radius: 0px;
        margin: 0px;
        background-color: $primary-color;
        border-color: $primary-color;
        font-size: 20px;
        #currentMonth{
            #npd-month-select,
            #npd-year-select{
                background-color: $primary-color;
                color: #fff;
                border: none;
                > option{
                    background-color: #fff;
                    color: #000;
                }
            }
        }
        .ndp-prev,
        .ndp-next{
            width: 25px;
            height: 25px;
            line-height: 25px;
            color: #fff !important;
            top: 2px !important;
            &:hover,
            &:focus{
                background: darken($primary-color, 5%);
            }
        }
        .ndp-next{
            &:after{
                content: "\f142";
                @extend %mdi-fonts;
            }
        }
        .ndp-prev{
            &:after{
                content: "\f141";
                @extend %mdi-fonts;
            }
        }
    }
    #npd-table-div{
        padding: 5px;
        table{
            width: 100%;
        }
        .ndp-days{
            font-weight: 600px;
        }
        .ndp-date{
            border: none !important;
            background: transparent !important;
            text-align: center;
            > a{
                color: $primary-color;
            }
            &:hover{
                background-color: #eee !important;
            }
        }
        .ndp-current{
            background-color: $primary-color;
            border-color: $primary-color;
            text-align: center;
            > a{
                color: #fff;
            }
        }
        .ndp-selected{
            background-color: lighten($primary-color, 35%);
            border-color: lighten($primary-color, 35%);
            text-align: center;
        }
    }
}