/* THEME-COLOR DEFINATION */
$primary-color: #e03051;
$accent-color: #465C81;

/* EXTRA-COLOR DEFINATION */
$highlight-color: #DC8937;
$balance-color: #33A3CD;
$calm-color: #af6ab0;
$positive-color: #46B663;
$assertive-color: #F46F5E;
$dark-color: #87919F;
$shade-color: #e8eaed;
$divider-color: #CCD0D4;

/* BACKGROUND-COLOR DEFINATION */
$background: #F6F6F6;
$dark-background: #36373D;
$light-background: #E9EBEC;

/* TEXT-COLOR DEFINATION */
$text-color: #4F5465;
$light-text-color: #727684;
$dark-text-color: #070707;

/* FUNCTION-COLOR DEFINATION */
$success-color: #19CD63;
$warning-color: #FFA949;
$danger-color: #F3554F;
$info-color: #409FC8;

/* FONT-SIZE DEFINATION */
$xl-font-size: 28px;
$lg-font-size: 22px;
$md-font-size: 16px;
$sm-font-size: 14px;
$xs-font-size: 12px;

/* FONT-WEIGHT DEFINATION */
$xl-font-weight: 700;
$lg-font-weight: 600;
$md-font-weight: 500;
$sm-font-weight: 400;
$xs-font-weight: 300;

/* FONT-FAMILY DEFINATION */
$title-font: 'Poppins', sans-serif;
$text-font: 'Roboto', sans-serif;
