.navbar-default{
	margin-bottom: 0px;
	@include backgroundGradient($primary-color, lighten($primary-color, 10%))
	border: none;
	.navbar-nav>.open>a,
	.navbar-nav>.open>a:focus,
	.navbar-nav>.open>a:hover{
		background-color: $primary-color;
	}
	.navbar-nav.navbar-right{
		float: right;
		margin: 0px;
	}
	.navbar-nav > li{
		float: left;
		ul.dropdown-menu{
			position: absolute;
		    top: 100%;
		    z-index: 1000;
		    min-width: 160px;
		    font-size: 14px;
		    background-color: #fff;
		    border: none;
		    box-shadow: 0 6px 12px rgba(0,0,0,.175);
			@include breakpoint(sm){

			}
		}
	}
}
.navbar-inverse{
	margin-bottom: 0px;
	background: $dark-background;
	border-color: $dark-background;
	box-shadow: 0px 1px 2px 0px darken($dark-background, 10%) inset;
	z-index: 9;
}
.navbar-brand{
	background-image: url(../images/logo-sm.png);
	background-repeat: no-repeat;
	background-position: center;
	width: 184px;
	height: 45px;
	display: block;
    float: none;
    margin: auto;
    @include breakpoint(sm){
	    margin-top: 5px;
    	margin-left: 0px !important;
    }
}
.page-header{
	border-color: $divider-color;
	padding: 5px 0px 5px 0px;
	margin: 0px;
	h1{
		font-weight: $md-font-weight;
		font-size: $xl-font-size;
		color: $text-color;
		margin: 0px;
		padding: 5px 0px 5px 0px;
	}
}
.breadcrumb{
	background: transparent;
	margin-top: 7px;
    margin-bottom: 5px;
    padding: 0px;
    font-size: $md-font-size;
    color: $dark-text-color;
    .active{
    	color: $dark-text-color;
    }
}
.form{
	&-group{
		label{
			font-weight: $lg-font-weight;
			em{
				display: block;
				font-size: $xs-font-size;
				font-weight: $sm-font-weight;
			    font-style: normal;
			    color: $light-text-color;
			}
		}
	}
	&-control{
		&:focus{
			box-shadow: none;
			border-color: $primary-color;
		}
	}
}
.btn{
	&-default{
		@include btn($shade-color, $light-text-color);
	}
	&-primary{
		@include btn($primary-color, #fff);
	}
	&-success{
		@include btn($success-color, #fff);
	}
	&-warning{
		@include btn($warning-color, #fff);
	}
	&-danger{
		@include btn($danger-color, #fff);
	}
	&-info{
		@include btn($accent-color, #fff);
	}
	&-link{
		color: $primary-color;
		&:hover,
		&:focus{
			text-decoration: none;
		}
	}
}
.panel{
	margin: 10px 0px;
	border-radius: 0px;
	border: none;
	box-shadow: 0px 0px 6px 0px lighten($dark-background, 55%);
	&-footer{
		border: none;
		background: transparent;
	}
	&-default{
		.panel-heading{
			border-radius: 0px;
			background: transparent;
			.panel-title{
				font-family: $title-font;
				font-size: $md-font-size;
				font-weight: $md-font-weight;
				color: $text-color;
				padding: 0px;
			}
		}
		.panel-body{
			//padding: 0px;
		}
	}
	&-primary{
		.panel-heading{
			padding: 15px 15px 10px 15px;
			border-radius: 0px;
			border-color: $divider-color;
			background: transparent;
			position: relative;
			&:after{
				content: "";
				height: 3px;
				width: 35px;
				background: $primary-color;
				position: absolute;
				bottom: -2px;
				left: 15px;
				border-radius: 4px;
			}
			.panel-title{
				color: $dark-text-color;
				font-family: $title-font;
				font-size: 18px;
				font-weight: $xs-font-weight;
				color: $text-color;
				padding: 0px;
			}
			.form-checkbox{
				position: absolute;
			    right: 15px;
			    top: 4px;
			}
		}
	}
}
.well{
	padding: 15px 20px;
	border: none;
	border-radius: 0px;
	box-shadow: none;
	margin-bottom: 0px;
}
.progress{
	height: 10px;
	border-radius: 0px;
	&-bar{
		&-primary{
			background-color: $primary-color;
		}
		&-default{
			background-color: $accent-color;
		}
		&-info{
			background-color: $info-color;
		}
		&-success{
			background-color: $success-color;
		}
		&-warning{
			background-color: $warning-color;
		}
		&-danger{
			background-color: $danger-color;
		}
	}
}
.text{
	&-primary{
		color: $primary-color;
	}
	&-default{
		color: $accent-color;
	}
	&-success{
		color: $success-color;
	}
	&-info{
		color: $info-color;
	}
	&-warning{
		color: $warning-color;
	}
	&-danger{
		color: $danger-color;
	}
}
.label{
	padding-left: 15px;
	padding-right: 15px;
	border-radius:25px;
	text-transform: uppercase;
	font-weight: $xs-font-weight;
	&-primary{
		background-color: $primary-color;
	}
	&-default{
		background-color: $accent-color;
	}
	&-success{
		background-color: $success-color;
	}
	&-info{
		background-color: $info-color;
	}
	&-warning{
		background-color: $warning-color;
	}
	&-danger{
		background-color: $danger-color;
	}
}

.nav{
	&-tabs{
		> li{
			> a{
				font-size: $sm-font-size;
				font-weight: $md-font-weight;
				border-radius: 0px;
				&:hover,
				&:focus{
					border-color: $background;
					background: $background;
				}
			}
			&.active{
				> a{
					
				}
				> a:hover,
				> a:focus{
					
				}
			}
		}
	}
}
.tab-content{
	padding: 15px;
	background: #fff;
	border: 1px solid #ddd;
	border-top: none;
}

.container{
	width: 100%;
	padding: 0px 30px;
}

.table{
	tr > th{
		background-color: #eee;
	}
}

.alert {
    border-radius: 0px;

    &.alert-success {
        @include alert($success-color);
    }

    &.alert-warning {
        @include alert($warning-color);
    }

    &.alert-danger {
        @include alert($danger-color);
    }

    &.alert-info {
        @include alert($info-color);
    }
}

.modal {
    &-header {
        padding: 15px 15px 10px 15px;
        position: relative;
        &:after {
            content: "";
            height: 3px;
            width: 35px;
            background: $primary-color;
            position: absolute;
            bottom: -2px;
            left: 15px;
            border-radius: 4px;
        }
    }

    &-content {
        border-radius: 0px;
    }

    &-title {
        color: $dark-text-color;
        font-family: $title-font;
        font-size: 18px;
        font-weight: $xs-font-weight;
        color: $text-color;
        padding: 0px;
    }
}