.btn{
	&-social-round{
		margin-top: 25px;
		.btn{
			&-facebook{
				@extend %btn-social-round;
				&:hover,
				&:focus{
					background: #4867AA;
					border-color: #4867AA;
					color: #fff;
				}
			}
			&-twitter{
				@extend %btn-social-round;
				&:hover,
				&:focus{
					background: #09A1EC;
					border-color: #09A1EC;
					color: #fff;
				}
			}
			&-linkedin{
				@extend %btn-social-round;
				&:hover,
				&:focus{
					background: #0077B5;
					border-color: #0077B5;
					color: #fff;
				}
			}
			&-google-plus{
				@extend %btn-social-round;
				&:hover,
				&:focus{
					background: #DC4C41;
					border-color: #DC4C41;
					color: #fff;
				}
			}
			&-youtube{
				@extend %btn-social-round;
				&:hover,
				&:focus{
					background: #CD201F;
					border-color: #CD201F;
					color: #fff;
				}
			}
		}
	}
	&-action{
		display: inline-block;
		border: none;
		width: 35px;
    	font-size: 16px;
		text-align: center;
		color: $light-text-color;
		border-right: 1px solid #ddd;
		&:last-child{
			border-right: none;
		}
		&:hover,
		&:focus{
			color: $primary-color;
			text-decoration: none;
		}
		i{
			margin: 0px;
		}
	}
}

.form{
	&-action{
		text-align: right;
	    margin-bottom: 0px;
	    > .btn{
	    	margin-right: 5px;
	    }
	}
	&-show{
		text-align: right;
		label{
			margin-right: 5px;
		}
	}
	&-filter{
		.btn-default{
			background: #f4f4f4;
			color: $text-color;
			border: 1px solid $divider-color;
			&:hover,
			&:focus,
			&:active,
			&:visited{
				border: 1px solid $divider-color !important;
			}
			i{
				color: $light-text-color;
			}
			.caret{
				margin-left: 10px;
				color: $light-text-color;
			}
		}
		.btn-sm{
			margin-top: 2px;
		}
	}
	&-checkbox{
		.btn-default{
			padding: 0px;
			background: #f4f4f4;
			color: $text-color;
			border: 1px solid $divider-color;
			text-transform: capitalize;
			&:hover,
			&:focus,
			&:active,
			&:visited{
				border: 1px solid $divider-color !important;
			}
			> label{
				padding: 5px 10px;
				position: relative;
			}
		}
		.btn-sm{
			margin-top: 2px;
		}
	}
}

.label{
	&-value{
		font-weight: $sm-font-weight !important;
	}
}
.checkbox{
	&-toogle{
	  display: inline-block;
		input{
			display:none;
		}
		.checkbox-track{
			position:relative;
			display: inline-block;
			box-sizing: border-box;
			width: 51px;
			height: 31px;
			border: solid 2px #e6e6e6;
			border-radius: 20px;
			background-color: #fff;
			content: ' ';
			cursor: pointer;
			pointer-events: none;
			.checkbox-handle{
				position:absolute;
				display: block;
				width: 27px;
				height: 27px;
				border-radius: 27px;
				background-color: #fff;
				box-shadow: 1px 1px 5px #aaa;
				}
		}
		input:checked + .checkbox-track{
		  border-color: #33cd5f;
		  background-color: #33cd5f;
		}
		input:checked + .checkbox-track .checkbox-handle{
		  right:0;
		}
	}
	&-inline{
	    padding-left: 30px !important;
		position:relative;
  		display:inline-block;
		ins{
			cursor: pointer;
			position: absolute;
			left: 8px;
			top: 8px;
			width: 15px;
			height: 15px;
			border-radius: 2px;		  
			display: block;
			border: 1px solid $divider-color;
			background-color: #fff;
		}
  		input{
		  visibility: hidden;
		  margin-left: 0px;
			&:checked + ins,
			&:checked + input + ins{
				background: $primary-color;
				border-color: darken($primary-color, 15%);
				&:after{
				    content: "\2714";
				    display: block;
				    color: #fff;
				    position: absolute;
				    left: 1px;
				    top: -2px;
				    font-size: 12px;
				}
			}
		}
	}
}

