.push{
	&-panel{
		background-color: #fff;
		box-shadow: 0px 0px 6px 0px lighten($dark-background, 55%);
		z-index: 9999;
	}
	&-header{
		padding: 10px 15px;
		border-bottom: 1px solid $divider-color;
		position: relative;
		margin-bottom: 10px;
		&:after{
			content: "";
			height: 3px;
			width: 35px;
			background: $primary-color;
			position: absolute;
			bottom: -2px;
			left: 15px;
			border-radius: 4px;
		}
	}
	&-title{
		color: $dark-text-color;
		font-family: $title-font;
		font-size: 18px;
		font-weight: $xs-font-weight;
		color: $text-color;
